* {
    box-sizing: border-box;
}
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh;
    display: flex;
    background: var(--bg-color-1);
    color: var(--color-1);
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
    display: flex;
    flex: 1;
    flex-direction: column;
}

main {
    flex: 1;
}

button {
    all: unset;
    cursor: pointer;
}
input {
    all: unset;
}
.ver-sep {
    width: 2px;
    border-radius: 1px;
    margin: 3px 0;
    background: var(--block-lighter-1);
    opacity: 0.7;
}
.icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    background: #9193c2;
}
.icon.question {
    mask: url('../media/question.ea5c2af94831fa14473c.svg') no-repeat center / contain;
    -webkit-mask: url('../media/question.ea5c2af94831fa14473c.svg') no-repeat center / contain;
}
