.formCardNum {
    background-color: #1E1E30; /* Dark background color */
    padding: 20px;
    border-radius: 10px; /* Rounded corners */
    width: 300px; /* Width of the form */
    color: white; /* Text color */
    font-family: 'Arial', sans-serif; /* Font style */
    margin: 30px 30px 0px 30px;
    height: min-content;
}

.formCardNum h3 {
    margin-bottom: 20px;
}

.formCardNum input[type="text"] {
    width: 92%;
    padding: 10px;
    margin-bottom: 20px;
    border: none;
    border-radius: 5px;
    background-color: #2C2C4A; /* Slightly lighter background for input */
    color: white;
}

.formCardNum select {
    width: 99%;
    padding: 10px;
    margin-bottom: 20px;
    border: none;
    border-radius: 5px;
    background-color: #2C2C4A; /* Slightly lighter background for select */
    color: white;
}

.formCardNum button {
    width: 92%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #4D4DE6; /* Purple background color */
    color: white;
    font-weight: bold;
    cursor: pointer;
    text-align: center;
}

.formCardNum button:hover {
    background-color: #3C3CB3; /* Darker shade on hover */
}